"use client";
import React from 'react';
import { useEffect, useState} from 'react';
import { useRouter } from 'next/navigation';
import { useUser } from "../components/UserContext";
import Pagination from "../components/Pagination";

import axios from "axios";

interface Business {
    _id: string;
    name: string;
    category: string;
    description: string;
}

export default function AdminDashboard() {
    const [businesses, setBusinesses] = useState<Business[]>([]);
    const { user, loading } = useUser(); 
    const router = useRouter();

    useEffect(() => {
        fetchBusinesses();
    }, []);

    useEffect(() => {
        if (!loading && (!user || user.role !== "Admin")) {
          router.replace("/unauthorized");
        }
      }, [user, loading, router]);

    const fetchBusinesses = async () => {
        try {
            const res = await fetch('http://localhost:5000/api/business/viewBizs');
            const data = await res.json();
            setBusinesses(data.businesses);
        } catch (error) {
            console.error('Failed to fetch Businesses', error);
        }
    };

    const handleDelete = async (id: String) => {
        const confirmed = confirm('are you sure you want to delete this business?');
        if(!confirmed) return;

        try {
            const res = await fetch('http://localhost:5000/api/business/deleteBiz/${id}', {
                method: 'DELETE'
            });

            if (res.ok) {
                setBusinesses(businesses.filter((biz) => biz._id !==id));
            } else {
                console.error('Failed to delete business');
            }
        } catch(error) {
            console.error('Delete error:', error);
        }
    };

    const handleUpdate = (id: string) => {
        router.push(`/update-business/${id}`);
    };

    return (
        <div className = 'p-6'>
            <h1 className="text-2xl font-bold mb-4">Admin Dashboard - All Businesses</h1>
            <table className='min-w-full border'>
                <thead className='bg-gary-100'>
                    <tr>
                        <th className='text-left p-2'>Name</th>
                        <th className='text-left p-2'>Category</th>
                        <th className='text-left p-2'>Description</th>
                        <th className='text-left p-2'>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {businesses.map((biz) => (
                        <tr key={biz._id} className='border'>
                            <td className="p-2">{biz.name}</td>
                            <td className="p-2">{biz.category}</td>
                            <td className="p-2 overflow-hidden text-ellipsis whitespace-nowrap">{biz.description}</td>
                            <td className='p-2 space-x-2'>
                                <button
                                    className='px-3 py-1 bg-sky-950 text-white rounded hover:opacity-50 cursor-pointer'
                                    onClick={() => handleUpdate(biz._id)}
                                >Update</button>
                            </td>
                            <td className='p-2 space-x-2'>
                                <button
                                    className='px-3 py-1 bg-pink-800 text-white rounded hover:opacity-50 cursor-pointer'
                                    onClick={() => handleDelete(biz._id)}
                                >
                                    Delete
                                </button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
}